import { NextAuthOptions } from "next-auth"
import CredentialsProvider from "next-auth/providers/credentials"

export const authOptions: NextAuthOptions = {
    providers: [
        CredentialsProvider({
            name: "Credentials",
            credentials: {
                email: { label: "Email", type: "text", placeholder: "hod@example.com" },
                password: { label: "Password", type: "password" }
            },
            async authorize(credentials) {
                console.log("[NextAuth] Authorize called with:", credentials)
                // Mock authentication logic
                if (!credentials?.email || !credentials?.password) {
                    console.log("[NextAuth] Missing credentials")
                    return null
                }

                const email = credentials.email.toLowerCase().trim()
                const password = credentials.password

                console.log(`[NextAuth] Checking: '${email}' with password '${password}'`)

                // HOD User
                if (email === "hod@example.com" && (password === "admin" || password === "hod123")) {
                    console.log("[NextAuth] Success: HOD matched")
                    return { id: "1", name: "HOD User", email: "hod@example.com", role: "hod" }
                }

                // Student User
                // Allow specific student credentials or generic fallback
                // Student User
                // 1. Explicit demo student login
                if (email === "student@example.com" && (password === "student" || password === "student123")) {
                    return { id: "2", name: "Student User", email: "student@example.com", role: "student" }
                }

                // 2. Roll Number Login (No @ symbol) -> MUST have Date Password
                // Enforce password to be a Date (YYYY-MM-DD) to prevent "anything" from logging in
                const isDatePassword = /^\d{4}-\d{2}-\d{2}$/.test(password);
                if (email && !email.includes("@") && isDatePassword) {
                    // Accepting non-email username ONLY if password looks like a DOB (YYYY-MM-DD)
                    // In a real app, you would look up the student by roll number (email variable here)
                    return { id: email, name: `Student ${email}`, email: `${email}@student.college.edu`, role: "student" }
                }

                // Faculty User
                if (email === "faculty@example.com" && (password === "faculty" || password === "faculty123")) {
                    return { id: "3", name: "Faculty User", email: "faculty@example.com", role: "faculty" }
                }

                // Placement User
                if (email === "placement@example.com" && (password === "placement" || password === "placement123")) {
                    return { id: "4", name: "Placement Officer", email: "placement@example.com", role: "placement" }
                }

                // Admin User (if needed)
                if (email === "admin@example.com" && (password === "admin" || password === "admin123")) {
                    return { id: "0", name: "System Admin", email: "admin@example.com", role: "admin" }
                }

                return null
            }
        })
    ],
    callbacks: {
        async jwt({ token, user }) {
            if (user) {
                token.role = user.role
            }
            return token
        },
        async session({ session, token }) {
            if (session.user) {
                session.user.role = token.role // Pass role to session
                session.user.id = token.sub || "" // Pass ID (Roll Number) to session
            }
            return session
        }
    },
    pages: {
        signIn: '/login', // Adjust if you have a specific login page
    }
}
